<template>
  <view class="container">
      <view v-for="(card,index) in cards" :key="index" class="cards">
        <text>{{ card.title }}</text>
        <image :src="card.bgImg" class="cover"/>
        <text>{{card.content}}</text>
      </view>
  </view>
</template>

<script>
export default {
  data(){
    return{
		title: 'hello',
        cards: [],  
    };
  },
  onLoad(){
